<template>
  <div>
    <el-card class="box-card">
      <stepsCmp></stepsCmp>

      <div class="group">
        <div class="box_content">
          <span class="title">拒绝退货原因</span>
          <el-input v-model="textarea" class="input" type="textarea" placeholder="请输入内容" maxlength="100" :autosize="{ minRows: 7, maxRows: 7 }" show-word-limit></el-input>
        </div>
        <div class="box_upload">
          <span class="title">上传凭证</span>
          <el-upload action="123" :http-request="onUpload" list-type="picture-card" :limit="5" :file-list="dialogImageUrl" multiple>
            <i class="el-icon-plus"></i>
          </el-upload>
          <p class="prompt">只支持JPG/PNG格式，最多可上传5张</p>
        </div>
        <div class="btn_group">
          <el-button type="success" size="small" style="width: 100px" round @click="onRefundStatus">发送</el-button>
          <el-button size="small" style="width: 100px" round @click="$router.back(-1)">取消</el-button>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
import stepsCmp from '@/views/order/refund_content/steps_cmp/index.vue'

export default {
  name: 'Refuse',

  components: {
    stepsCmp
  },

  data() {
    return {
      dialogImageUrl: [],
      dialogVisible: false,
      textarea: ''
    }
  },
  methods: {
    // 点击上传图片
    onUpload (params) {
      const isJPG = params.file.type === 'image/jpeg' || params.file.type === 'image/png'
      if (!isJPG) {
        this.$message.error('上传图片只能是 JPG/PNG 格式!')
        return
      }
      const formData = new FormData()
      formData.append('file', params.file)
      this.apiUpload(formData)
    },
    // 点击发送
    onRefundStatus () {
      console.log()
      this.apiRefundStatus()
    },

    // api------------------------------------------
    // 未同意退款退货
    apiRefundStatus() {
      let data = {
        returnType: 0,
        approveReason: this.textarea,
        id: this.$route.query.id,
      }
      this.$http.post('/woho/order/returnStock/refundReturnStatus', data)
        .then(res => {
          if (res.data.success) {
            console.log(res)
            this.$message.success('拒绝退货退款申请')
            this.$router.go(-1)
          } else {
            this.$message.success(res.data.msg)
          }
        })
    },
    // 上传图片
    apiUpload(formData) {
      this.$http.post('/common/oss/uploadFile', formData)
        .then((res) => {
          console.log(res.data.data.webUrl)
          this.dialogImageUrl = res.data.data.webUrl
        })
    },
  }
}
</script>

<style lang="scss" scoped>
  .box-card {
    min-width: 1100px;
    margin-top:15px;
    .group{
      font-size: 14px;
      width: 54.4%;
      margin: 107px auto 0px auto;
      .box_content{
        position: relative;
        .title{
          position: absolute;
          top: 0px;
          left: -100px;
        }
      }
      .box_upload{
        margin-top: 72px;
        position: relative;
        .title{
          position: absolute;
          top: 0px;
          left: -100px;
        }
        .prompt{
          color: #999999;
          font-size: 12px;
        }
      }
      .btn_group{
        margin-top: 56px;
      }
    }
  }
</style>
